.brand-cont{
    display: flex;
    flex-flow: column nowrap;   
    height: 100vh; 
    justify-content: center;
    align-items: center;
    background-color: white;
}

.brand-logo{
    width: 400px;
    height: 400px;
    flex-grow: 0;
}

.brand-cont p a{
    color: black;
    font-size: 18px;
    font-weight: bolder;
    text-decoration: underline;
    text-transform:uppercase;
}

.about-cont{
    display: flex;
    flex-flow: row wrap;  
    background-color:darkblue;  
    justify-content: center;
    align-items: center;
    min-height: 700px;
    padding: 40px;
}
.about-cont img{
    height: 500px;
    width:  500px;
    background-color: white;
    border-radius: 10px;
    padding:70px;
}

.about-desp{
    flex-grow: 1;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    max-height: 500px;
    max-width: 700px;
    min-width: 400px;
    padding: 15px;
    font-family:Fantasy;
}

.about-desp-head{
    font-size: 45px;
    font-weight:bolder;
    color:white;
}

.about-desp-body{
    font-size: 21px;
    color:white;
}

.about-cont img{
    width: 400px;
    height: 400px;
    flex-grow: 0;
}
